const UseEffect = () => {
    const [state, setState] = useState(0)
    useEffect(() => {
        document.title = `Clicked(${state})`
    })
    return (
        <>
            <div className="state-wrap mb-4">
                <h2 className="mb-3 text-primary">Nested Components (hooks)</h2>
                <hr />
                <h4>Basics of useEffect</h4>
                <button className="btn btn-warning me-3" onClick={() => setState(state + 1)}>doc title updating with each click</button>
            </div>
        </>
    )
}

export default Contact
